.app-wrapper {
  display: grid;

  max-width: 1200px;
  margin: 0 auto;

  grid-template-areas: "header header" "nav content";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px;

  background-color: #f2f2f2;
}

.app-wrapper-content {
  grid-area: content;
  background-color: #fff;
}
